// Grid
@import "susy";

// Breakpoint
@import "breakpoint";

@include add-breakpoint('small', 320px);
@include add-breakpoint('medium', 640px);
@include add-breakpoint('large', 920px);
@include add-breakpoint('wide', 1200px);
@include add-breakpoint('mega', 1400px);

// Default container
%container {
  @include container(100%);
  margin: 0 rhythm(1);

  @include respond-to('large') {
    @include container(750px); }

  @include respond-to('wide') {
    @include container(1170px); } }



