@import "../typography";

// Base typography
html {
  font-size: 0.625em; }

body {
  font-family: Roboto, serif;
  font-weight: 300;
  color: #333;
  @include type-setting(1); }

p {
  margin-bottom: rhythm(1); }

strong {
  font-weight: 400; }

.ms-down-1 {
  @include type-setting(-1); }

@for $i from 1 through $ms-levels {
  .ms-up-#{$i} {
    font-weight: 100;
    @include type-setting($i); } }

.ms-up-6 {
  margin-bottom: rhythm(3); }

.ms-up-5 {
  margin-bottom: rhythm(3); }

.ms-up-4 {
  margin-bottom: rhythm(2); }

.ms-up-3 {
  margin-bottom: rhythm(2); }

.ms-up-2 {
  margin-bottom: rhythm(1); }

.ms-up-1 {
  margin-bottom: rhythm(1); }



